@font-face {
    font-family: 'PTSans';
    src: url('../../fonts/PTSansRegular.woff2') format('woff2'),
         url('../../fonts/PTSansRegular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
  }

  @font-face {
    font-family: 'PTSans';
    src: url('../../fonts/PTSansBold.woff2') format('woff2'),
         url('../../fonts/PTSansBold.woff') format('woff');
    font-weight: 700;
    font-style: normal;
  }

  $mainFont: 'PTSans', sans-serif;

  body {
      background-color: #f2f2f2;
      font-family: $mainFont;
      text-transform: uppercase;
      font-size: 1rem;
  }
  .container {
      max-width: 1200px;
      margin: 0 auto;
      @media (max-width: 700px) {
        background-color: #ffffff;
        padding: 0 10px;
      }
  }
  .item-title {
    font-size: 1.3rem;
    line-height: 1.3;
    margin-bottom: 1em;
    font-weight: 700;
 }
 .cause {
   font-size: 1rem;
   line-height: 1.4;
   margin-top: 2em;
 }
 .form-box-title {
    font-size: 1.3rem;
    color: #000000;
    font-weight: 700;
    margin-bottom: 3em;
    text-align: center;
    margin-bottom: 3em;
    position: relative;
    @media (max-width: 650px) {
        margin-bottom: 1em;
      }
    &::before,
    &::after {
        content: '';
        width: 28%;
        height: 1px;
        background: #dbdbdb;
        display: block;
        position: absolute;
        top: 8px;
        @media (max-width: 650px) {
            display: none;
          }
    }
    &::before {
        left: 0;
    }
    &::after{
        right: 0;
    }
  }
  header {
      .main-header-nav {
        @media (max-width: 700px) {
            display: none;
          }
        .nav-list {
            display: flex;
            background-color: #ffffff;
            justify-content: space-around;
            li:not(:nth-child(3)) {
                padding: 1.29em 0 1.29em 0;
            }
            li:nth-child(3) {
                a {
                    display: block;
                    position: relative;
                    img {
                        position: absolute;
                        display: block;
                        top: 0;
                        left: -94px;
                    }
                }
            }
            a{
              color: #000000;
              font-weight: 700;
              line-height: 1.86;
              &:hover {
                  color: #9ac2db;
              }
              &:active {
                  color: #000000;
                  opacity: 0.3;
              }
              &.active-item {
                  color: #938e8e;
              }
            }
        }
      }

      .mobile-menu {
          display: none;
          @media (max-width: 700px) {
            display: block;
          }
          .mobile-menu-list {
              display: none;
              margin: 0 -10px;
              .mobile-menu-list-item {
                  text-align: center;
                  border-bottom: 1px solid #678fa8;
                  border-top: 1px solid #9ac2db;
                  &:last-child {
                      border-bottom: none;
                  }
                  a {
                      color: #ffffff;
                      font-size: 1.28rem;
                      font-weight: 700;
                      display: block;
                      padding: 1.11em 0;
                      background-color: #81b3d2;
                  }
              }
          }
          .mobile-header {
              background-color: #ffffff;
              .mobile-header-nav {
                  max-width: 50%;
                  display: flex;
                  margin-left: auto;
                  justify-content: space-between;
                  .image-item {
                      margin-left: -51px;
                      position: relative;
                      img {
                          position: absolute;
                          top: 0;
                      }
                  }
                  .header-burger {
                    display: block;
                    padding: 2em 0;
                    position: relative;
                    width: 23px;
                    height: 21px;
                    z-index: 10;
                    cursor: pointer;
                    &::before,
                    &::after {
                        content: '';
                        background-color: #81b3d2;
                        position: absolute;
                        width: 100%;
                        height: 4px;
                        left: 0;
                    }
                    &::before {
                        top: 16px;
                    }
                    &::after {
                        bottom: 16px;
                    }
                    span {
                        position: absolute;
                        background-color: #81b3d2;
                        left: 0;
                        width: 100%;
                        height: 4px;
                        top: 26px;
                    }
                  }
                  .exit {
                    width: 23px;
                    height: 23px;
                    position: relative;
                    padding: 2em 0;
                    display: none;
                    cursor: pointer;
                    &::before,
                    &::after {
                        position: absolute;
                        left: 0;
                        top: 15px;
                        content: ' ';
                        height: 23px;
                        width: 4px;
                        background-color: #81b3d2;;
                    }
                    &::before {
                        transform: rotate(45deg);
                    }
                    &::after {
                        transform: rotate(-45deg);
                    }
                  }
              }
          }
      }
  }
  .promo {
      .box {
          padding: 5.57em 0 6.43em 0;
          background: url('../../build/img/bg.jpg') no-repeat center;
          background-size: cover;
          text-align: center;
          @media (max-width: 700px) {
            margin: 0 -10px;
          }
            img {
              max-width: 100%;
              margin-left: -35px;
              @media (max-width: 700px) {
                margin-left: 0px;
              }
            }
          
      }
  }
  .promo-text {
      .box {
        padding: 3.57em 0 3.57em 0;
        background-color: #ffffff;
        .box-body {
            max-width: 490px;
            @media (max-width: 420px) {
                max-width: 282px;
              }
            margin: 0 auto;
            text-align: center;
            .text-title {
                color: #000000;
                font-size: 1.3rem;
                font-weight: 700;
                margin-bottom: 1.92em;
                line-height: 1.3;
            }
            .text {
                color: #333333;
