  }
  .causes {
      .box {
          display: flex;
          @media (max-width: 700px) {
            margin: 0 -10px;
          }
          @media (max-width: 620px) {
            flex-direction: column;
          }
          .tex-item {
              flex: 0 1 30%;
              max-width: 30%;
              background-color: #81b3d2;
              color: #ffffff;
              text-align: center;
              padding: 5.07em 3.29em 2.36em 3.29em;
              @media (max-width: 620px) {
                flex: 0 1 auto;
                max-width: 100%;
                padding: 2.36em 3.29em 2.36em 3.29em;
              }
          }
          .image-item {
              flex: 0 1 70%;
              max-width: 70%;
              @media (max-width: 620px) {
                flex: 0 1 auto;
                max-width: 100%;
              }
                img {
                  width: 100%;
                  height: 100%;
                  object-fit: cover;
              }
