                    &:last-child {
                       padding-bottom: 2.5em; 
                    }
                  }
                  &.item1 {
                      .icon {
                          background: url('../../build/img/house.svg') no-repeat;
                          width: 75px;
                          height: 75px;
                          margin: 0 auto;
                          margin-bottom: 1.5em;
                      }
                  }
                  &.item2 {
                    .icon {
                        background: url('../../build/img/food.svg') no-repeat;
                        width: 77px;
                        height: 75px;
                        margin: 0 auto;
                        margin-bottom: 1.5em;
                    }
                }
                &.item3 {
                    .icon {
                        background: url('../../build/img/gift.svg') no-repeat;
                        width: 64px;
                        height: 75px;
                        margin: 0 auto;
                        margin-bottom: 1.5em;
                    }
                }
                  h2 {
                    font-weight: 700;
                    color: #000000;
                    font-size: 1.3rem;
