  /*---form---*/
  .form-header {
      .container {
        max-width: 1200px;
        background-color: #ffffff;
        padding: 0;
      }
  }
  .form-main {
      .container {
          max-width: 1200px;
          background-color: #ffffff;
          padding: 0;
      }
  }
  .bg {
      .box {
        text-align: center;
        padding-top: 7.07em;
        height: 305px;
        background: url('../../build/img/BackgroundBottom.png') bottom no-repeat,
                    url('../../build/img/Background.jpg') bottom center no-repeat;
                    img {
                        max-width: 100%;
                    }
      }
  }
  .form {
    .form-container {
        max-width: 960px;
        margin: 0 auto;
        padding: 6.5em 10px 0 10px;
        @media (max-width: 768px) {
            max-width: 648px;
          }
          @media (max-width: 650px) {
            padding: 3.5em 10px 0 10px;
          }
    }
      .form-box {
          max-width: 439px;
          margin: 0 auto;
          text-align: center;
          .title {
              font-size: 1.8rem;
              font-weight: 700;
              line-height: 1.3;
              margin-bottom: 1em;
          }
          .text {
            color: #333333;
            font-size: 0.9rem;
            font-weight: 400;
            line-height: 1.8;
          }
      }
      form {
          margin-top: 5.57em;
          margin-bottom: 4.57em;
          @media (max-width: 650px) {
            margin-top: 3.57em;
          }
          .row {
              display: flex;
              justify-content: space-between;
              max-width: 960px;
              margin: 0 auto;
              @media (max-width: 650px) {
                flex-direction: column;
              }
              .column {
                  flex: 0 1 41.66%;
                  max-width: 41.66%;
                  height: 100%;
                  @media (max-width: 650px) {
                    flex: 0 1 auto;
                    border-top: 1px solid #e5e5e5;
                    max-width: 100%;
                    text-align: center;
                    padding-top: 2em;
                    &:last-child {
                        margin-top: 4em;
                    }
                  }
                  .form-item {
                    height: 100%;
                      h3 {
                        font-size: 1.3rem;
                        color: #000000;
                        font-weight: 700;
                        margin-bottom: 3em;
                      }
                      .radiobuttons {
                        @media (max-width: 650px) {
                            text-align: left;
                            }
                          .radiobutton-item {
                            font-size: 1.1rem;
                            font-weight: 700;
                            position: relative;
                            padding: 0 0 0 4em;
                            margin-bottom: 3em;
                            text-transform: none;
                            cursor: pointer;
                            &:last-child {
                                margin-bottom: 0;
                            }
                            &.active {
                                &::after {
                                    transform: scale(1);
                                }
                            }
                            input {
                                display: none;
                            }
                                &::before {
                                    content: '';
                                    width: 40px;
                                    height: 40px;
                                    background-color: #f2f2f2;
                                    border-radius: 50px;
                                    position: absolute;
                                    left: 0;
                                    top: calc(50% - 20px);
                                }
                                &::after{
                                    content: '';
                                    width: 10px;
                                    height: 10px;
                                    background-color: #333333;
                                    border-radius: 50px;
                                    position: absolute;
                                    left: 15px;
                                    top: calc(50% - 5px);
                                    transform: scale(0);
                                }
                          }
                      }
                      .name {
                          margin-top: -1.5em;
                          .name-item {
                              font-size: 1rem;
                              display: flex;
                              align-items: center;
                              color: #a0a0a0;
                              margin-bottom: 1.2em;
                              text-transform: capitalize;
                              &:last-child {
                                  margin-bottom: 0;
                              }
                              input {
                                  margin-left: 1em;
                                  flex: 0 1 100%;
                                  background-color: #f2f2f2;
                                  padding: 1em;
                                  font-weight: 700;
                                  max-width: 100%;
                                  text-transform: capitalize;
                                  color: #000000;
                                  &:hover {
                                    background-color: #e5e5e5;
                                }
                                &:focus {
                                    background-color: #ffffff;
                                    box-shadow: 0px 0px 0px 4px #e5e5e5;
                                }
                                
                              }
                          }
                      }
                  }
              }
          }
          .contact-information {
              margin-top: 6em;
              @media (max-width: 768px) {
                margin-top: 2em;
              }
              @media (max-width: 650px) {
                border-top: 1px solid #e5e5e5;
                padding-top: 2em;
              }
              .row {
                  display: flex;
                  justify-content: space-between;
                  @media (max-width: 768px) {
                    flex-direction: column;
                  }
