                            margin-top: 3em;
                          }
                          @media (max-width: 650px) {
                            margin-top: -2em;
                          }
                        &::after {
                          background: url('../img/mail.svg') no-repeat center;
                        }
                    }
                      .telephone-item, 
                      .email-item {
                          font-size: 1rem;
                          color: #a0a0a0;
                          text-transform: none;
                          align-items: center;
                          display: flex;
                          @media (max-width: 768px) {
                            flex-direction: column;
                            align-items: flex-start;
                          }
