                            flex: 0 1 auto;
                            max-width: 100%;
                            &:last-child {
                                margin-bottom: 0;
                            }
                          }
                        &.active {
                            &::after {
                                transform: scale(1);
                            }
                        }
                        &::before {
                            content: '';
                            width: 36px;
                            height: 36px;
                            border-radius: 5px;
                            background:  #f2f2f2;
                            position: absolute;
                            left: 0;
                            top: calc(50% - 18px);
                        }
                        &::after{
                            content: '';
                            width: 10px;
                            height: 10px;
                            background: url('../img/tick.svg') no-repeat center;
                            background-size: 100%;
                            position: absolute;
                            left: 13px;
                            top: calc(50% - 5px);
                            transform: scale(0);
                        }
                    input {
                        display: none;
                    }
                }
            }
          }
          .emotions {
            margin-top: 6em;
            @media (max-width: 650px) {
                margin-top: 2em;
                border-top: 1px solid #e5e5e5;
                padding-top: 2em;
              }
            .textarea {
                textarea {
                   width: 100%;
                    background-color: #f2f2f2;
                    padding: 1.2em;
                    color: #000000;
                    font-weight: 700;
                    height: 160px;
                    resize : none;
                    font-size: 1rem;
                    &:focus {
                        background-color: #ffffff;
                        box-shadow: 0px 0px 0px 4px #e5e5e5;
                    }
                }
            }
          }
          .button-box {
              margin-top: 4em;
              display: flex;
              align-items: center;
              justify-content: space-between;
              .button {
                  text-align: center;
                  flex: 0 1 50%;
                  max-width: 50%;
                  button {
                      width: 100%;
                      display: block;
                      text-align: center;
                      font-size: 1.2rem;
                      color: #ffffff;
                      padding: 1em 0;
                      text-transform: uppercase;
                      background-color: #81b3d2;
                      font-weight: 700;
                  }
              }
              .required {
                  color: #333333;
                  font-size: 0.8rem;
                  flex: 0 1 50%;
                  max-width: 50%;
                  text-align: right;
              }
          }
      }
  }
  .photo-main {
      .container {
        background-color: #ffffff;
      }
      .bg {
          margin: 0 -10px;
      }
      .photo-video {
          padding-top: 6.14em;
          .photo-video-body {
              max-width: 440px;
              margin: 0 auto;
              text-align: center;
              margin-bottom: 4.57em;
              .photo-video-title {
                font-size: 1.8rem;
                font-weight: 700;
                color: #000000;
                font-weight: 700;
                line-height: 1.3;
                margin-bottom: 1em;
                text-align: center;
              }
              .photo-video-description {
                font-size: 0.9rem;
                color: #333333;
                line-height: 1.8;
              }
         
          }
          .photo-box {
              display: flex;
              flex-wrap: wrap;
              margin: 0 -10px 6em -10px;
              @media (max-width: 650px) {
                flex-wrap: nowrap;
                flex-direction: column;
              }
              .photo-box-item {
                flex: 0 1 33.3333%;
                max-width: 33.3333%;
                position: relative;
                @media (max-width: 650px) {
                    flex: 0 1 100%;
                    max-width: 100%;
                    border-bottom: 1px solid #e5e5e5;
                    padding-top: 3em;
                    &:first-child {
                        border-top: 1px solid #e5e5e5;
                    }
                  }
                  &:first-child {
                      flex: 0 1 100%;
                      max-width: 100%;
                  }
                  img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    display: block;
                    &.show {
                        display: none;
                        @media (max-width: 650px) {
                            display: block;
                            height: none;
                            object-fit: none;
                          }
                    }
                    &.hide {
                        @media (max-width: 650px) {
                            display: none;
                          }
                    }
                    @media (max-width: 650px) {
                        max-width: 450px;
                        max-height: 450px;
                        margin: 0 auto;
                      }
                }
                .text-item {
                    padding: 1.1em;
                    position: absolute;
                    bottom: 0;
                    align-items: center;
                    left: 0;
                    right: 0;
                    display: flex;
                    justify-content: space-between;
                    background-color: rgba(19, 15, 14, 0.3);
                    width: 100%;
                    @media (max-width: 650px) {
                        position: static;
                        background-color: #ffffff;
                        max-width: 450px;
                        margin: 0 auto;
                      }
                    .text-body {
                        color: #ffffff;
                        @media (max-width: 650px) {
                            color: #000000;
                          }
                        .title {
                            font-weight: 700;
                            font-size: 1rem;
                        }
                        .author {
                            font-size: 0.9rem;
                            text-transform: none;
                            line-height: 1.7;
                        }
                    }
                    .icon-item {
                        display: flex;
                        align-items: center;
                        color: #ffffff;
                        @media (max-width: 650px) {
                            color: #000000;
                          }
                        p {
                            margin-left: 1em;
                        }
                    }
                }
              }
          }
          .video-box {
              max-width: 800px;
              margin: 0 auto;
              padding-bottom: 3em;
              img {
                  max-width: 100%;
              }
          }
      }
  }